import { Directive, Renderer2, ElementRef, OnInit, Input } from '@angular/core';

@Directive({
  selector: '[appProgress]'
})
export class ProgressDirective implements OnInit {
  @Input() Pwidth: number;
  @Input() Pcolor: string;
  @Input() Ptime: number = 0;

  timeId: any;
  width: number = 0;

  constructor(private renderer: Renderer2, private el: ElementRef) { }

  ngOnInit() {
    this.renderer.setStyle(this.el.nativeElement, 'backgroundColor', this.Pcolor);
    Text = this.renderer.createText(`${this.Pwidth}%`);
    this.renderer.appendChild(this.el.nativeElement, Text);

    if (this.Ptime <= 0) {
      this.Ptime = 300;
    }

    this.timeId = setInterval(() => {
      if (this.width < this.Pwidth) {
        this.width++;
        this.renderer.setStyle(this.el.nativeElement, 'width', `${this.width}%`);
      } else {
        clearInterval(this.timeId);
      }
    }, this.Ptime);
  }

}
